<template>
  <div>
      <my-table :data="tableData" :showPin="showTablePin" :page="page" @on-size-change="handleSizeChange" @on-current-change="handleCurrentChange" @on-selection-change="handleSelectionChange"
                @on-edit="edit">
      </my-table>
  </div>
</template>

<script>
  import apiRequest from '../../utils/apiRequest.js'
  import myTable from '../../components/common/myTable.vue'
  import myDialog from '../../components/common/myDialog.vue'
  import crud from '../../mixins/crud.js'
  import ClientConnection from "../monitor/clientConnection";

  export default {
    name: "brokerConnectionMonitor",
    components: {
      ClientConnection,
      myTable,
      myDialog
    },
    props:{
      brokerId:{
        type:Number
      }
    },
    mixins: [ crud ],
    data() {
      return {
        theme1: 'light',
        urls: {
          search: '/monitor/broker/connection/search',
        },
        searchData: {
          brokerId: this.brokerId,
        },
        searchRules: {
        },
        tableData: {
          rowData: [],
          colData: [
            {
              title: 'ID',
              key: 'connectionId'
            },
            {
              title: '',
              key: 'app'
            },
            {
              title: '',
              key: 'version'
            },
            {
              title: '',
              key: 'language'
            },
            {
              title: '',
              key: 'source'
            },
            {
              title: '',
              key: 'createTime'
            },
            {
              title: 'ip',
              key: 'ip'
            },
            {
              title: '',
              key: 'port'
            },
            {
              title: '',
              key: 'namespace'
            },
            {
              title: '',
              key: 'producerRole',
              formatter (item) {
                if (item.producerRole) { return '' }
                if (item.consumerRole) { return '' }
              }

            }
          ],
          //  bindKeybindVal
          // btns: [
          //   {
          //     txt: '',
          //     method: 'on-edit'
          //   }
          // ]
        },
        multipleSelection: []
      }
    },
    methods: {
    },
    mounted () {
      this.getList()
    }
  }
</script>



