<template>
  <div>
    <div class="ml20 mt10">
      <d-input v-model="keyword" :placeholder="keywordTip" class="left" style="width: 60%">
        <icon name="search" size="14" color="#CACACA" slot="suffix" @click="getList"></icon>
      </d-input>
    </div>
    <my-table :data="tableData" :showPin="showTablePin" :page="page" @on-size-change="handleSizeChange"
              @on-current-change="handleCurrentChange" @on-selection-change="handleSelectionChange" @on-add="add">
    </my-table>
  </div>
</template>

<script>
import apiRequest from '../../utils/apiRequest.js'
import myTable from '../../components/common/myTable.vue'
import crud from '../../mixins/crud.js'
import apiUrl from '../../utils/apiUrl.js'

export default {
  name: 'subscribe',
  components: {
    myTable
  },
  props: {
    keywordTip: {
      type: String,
      default: '/'
    },
    operates: {
      type: Array
    },
    colData: {// 
      type: Array
    },
    btns: {
      type: Array,
      default: function () {
        return [
          {
            txt: ' ',
            method: 'on-add'
          }
        ]
      }
    },
    doSearch: {
      type: Boolean,
      default: false
    },
    search: {// app:{id:0,code:'',namespace:{id:0,code:''}}   topic:{id:0,code:'',namespace:{id:0,code:''}}
      type: Object
    },
    type: { // 1- 2-
      type: Number,
      default: 1
    },
    addUrl: '',
    searchUrl: ''
  },
  mixins: [ crud ],
  data () {
    return {
      urls: {
        search: this.searchUrl,
        add: this.addUrl
      },
      keyword: '',
      tableData: {
        rowData: [],
        colData: this.colData,
        btns: this.btns,
        operates: this.operates
      },
      multipleSelection: [],
      subscribeGroupList: []
    }
  },
  methods: {
    // 
    getList () {
      this.showTablePin = true
      let data = {
        pagination: {
          page: this.page.page,
          size: this.page.size
        },
        query: {
          keyword: this.keyword,
          subscribeType: this.type
        }
      }
      console.log(22)
      console.log(this.search)
      for (var key in this.search) {
        if (this.search.hasOwnProperty(key)) {
          data.query[key] = this.search[key]
        }
      }
      // this.tableData.rowData = [] // 
      apiRequest.post(this.urlOrigin.search, {}, data).then((data) => {
        data.data = data.data || []
        data.pagination = data.pagination || {
          totalRecord: data.data.length
        }
        this.page.total = data.pagination.totalRecord
        this.page.page = data.pagination.page
        this.page.size = data.pagination.size
        this.tableData.rowData = data.data.map(d => {
          d['subscribeGroup'] = ''
          return d
        })
        this.showTablePin = false
      })
    },
    // 
    add (item) {
      let data = {}
      // Validate
      // Consumer must input clientType and subscribeGroup.
      // Producer must input clientType.
      if (item.clientType === undefined) {
        this.$Dialog.warning({
          content: ''
        })
        return
      }
      data.clientType = item.clientType
      if (this.type === this.$store.getters.consumerType) {
        // if (!item.subscribeGroup) {
        //   this.$Dialog.warning({
        //     content: ''
        //   })
        //   return
        // }
        if (item.subscribeGroup && !item.subscribeGroup.match(this.$store.getters.pattern.subscribeGroup)) {
          this.$Dialog.warning({
            content: '' + this.$store.getters.placeholder.subscribeGroup
          })
          return
        }
        data.subscribeGroup = item.subscribeGroup || ''
      }
      for (var key in this.search) {
        if (this.search.hasOwnProperty(key)) {
          data[key] = this.search[key]
          if (key === 'app') {
            data.topic = {
              id: item.id,
              code: item.code
            }
            data.namespace = item.namespace
          } else if (key === 'topic') {
            data.app = {
              id: item.id,
              code: item.code
            }
            // data.namespace = this.search.topic.namespace
          }
        }
      }

      let _this = this
      this.$Dialog.confirm({
        title: '',
        content: ''
      }).then(() => {
        apiRequest.post(_this.addUrl, null, data).then(() => {
          // 
          this.tableData.rowData = []
          // 
          _this.getList()
          _this.$emit('on-refresh')
        })
      }).catch(() => {
      })
    },
    getSubscribeGroups () {
      apiRequest.get(apiUrl.common.findSubscribeGroup).then((data) => {
        this.subscribeGroupList = data.data || []
      })
    },
    querySubscribeGroup (queryString, callback) {
      var subscribeGroups = this.subscribeGroupList
      var results = queryString ? subscribeGroups.filter(subscribeGroup => {
        return subscribeGroup.toLowerCase().indexOf(queryString.toLowerCase().trim()) === 0
      }) : subscribeGroups
      //  callback 
      callback(results)
    }
  },
  mounted () {
    this.getList()
  },
  watch: {
    doSearch: {
      handler (curVal, oldVal) {
        if (curVal) {
          this.getList()
          this.doSearch = false
        }
      },
      deep: true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



