/**
 *data
 * @param fmt
 * @returns {*}
 * @constructor
 */
Date.prototype.Format = function (fmt) { // author: meizz
  var o = {
    'M+': this.getMonth() + 1, // 
    'd+': this.getDate(), // 
    'h+': this.getHours(), // 
    'm+': this.getMinutes(), // 
    's+': this.getSeconds(), // 
    'q+': Math.floor((this.getMonth() + 3) / 3), // 
    'S': this.getMilliseconds() // 
  }
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length))
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return fmt
}

/**
 * 
 * @param nS
 * @returns {*}
 */

export function timeStampToDate (nS) {
  var data = new Date()
  data.setTime(nS)
  return data
}

/**
 * 
 * @param nS
 * type false:nstruens-
 * isHour false , true
 * @returns {*}
 */
export function timeStampToString (nS, type, isHour) {
  var data = new Date()
  if (nS) {
    data = timeStampToDate(nS)
  } else {
    if (type) {
      return '-'
    }
  }

  var format = 'yyyy-MM-dd hh:mm:ss'
  if (isHour) {
    format = 'hh:mm:ss'
  }
  return data.Format(format)
}

/**
 * yyyyMMddhhmmss
 * @returns {*}
 */
export function getNowTime () {
  let data = new Date()
  return data.Format('yyyyMMddhhmmss')
}

export function timeStampToDayString (nS, type) {
  var data = new Date()
  if (nS) {
    data = timeStampToDate(nS)
  } else {
    if (type) {
      return '-'
    }
  }

  var format = 'yyyy-MM-dd'
  return data.Format(format)
}

// 
export function format (time, type) {
  var data = new Date()
  data.setTime(time)
  if (!type) {
    type = 'yyyy-MM-dd hh:mm:ss:S'
  }
  return data.Format(type)
}

/**
 *   '2015-03-05 17:59:01.0';
 * @param Str
 * @constructor
 */
export function stringToTimeStamp (str) {
  str = str.replace(/-/g, '/')
  let timestamp = new Date(str).getTime()
  return timestamp
}



