<template>
  <div class="table">
    <d-table
      class="tablePadding"
      style="word-wrap:break-word;word-break:break-all;"
      :optional="optional"
      :columns="ColOpData"
      :data="rowData"
      :height="height"
      @row-click="handleRowClick"
      @on-selection-change="handleSelectionChange"
      @on-select-all="handleSelectionAll">
      <d-table-column v-if="data.selectable"
        type="selection"
        width="55">
      </d-table-column>
    </d-table>
    <d-pagination  class="right mr20"
      v-if="showPagination"
      show-total show-sizer show-quickjump
      :page-size-opts="[10, 20, 50, 100]"
      :current="page.page"
      :page-size="page.size"
      :total="page.total"
      @pagesize-change="handleSizeChange"
      @page-change="handleCurrentChange">
    </d-pagination>
    <d-spin fix size="large" v-if="showPin"/>
  </div>
</template>

<script>
import store from '../../store'
import viewLang from '../../i18n/views.json'
import {deepCopy} from '../../utils/assist.js'

export default {
  name: 'myTable',
  props: {
    optional: {
      type: Boolean,
      default: false
    },
    showPagination: {
      type: Boolean,
      default: true
    },
    showPin: {
      type: Boolean,
      default: false
    },
    height: {
      type: String,
      default: ''
    },
    data: {
      rowData: [],
      colData: [],
      inputs: [],
      btns: [],
      operates: []
    },
    page: {
      page: 1,
      size: 10,
      total: 100
    }
  },
  data () {
    return {
    }
  },
  computed: {
    curLang () {
      return this.$i18n.locale
    },
    langConfig () {
      return viewLang[this.curLang]['common']
    },
    rowData () {
      return this.data.rowData
    },
    ColOpData () {
      let inputsOrigin = this.data.inputs || []
      let btnsOrigin = this.data.btns || []
      let operatesOrigin = this.data.operates || []
      let colOpData = deepCopy(this.data.colData) || []
      // input
      for (let i = 0; i < inputsOrigin.length; i++) {
        let title = inputsOrigin[i].title
        colOpData.push({
          title: title,
          render: (h, params) => {
            let vm = this
            let key = params.item
            let keyArr = inputsOrigin[i].key.split('.')
            for (let i = 0; i < keyArr.length - 1; i++) {
              if (key[keyArr[i]] === undefined) {
                key[keyArr[i]] = {}
              }
              key = key[keyArr[i]]
            }
            return h('DInput', {
              domProps: {
                value: ''
              },
              on: {
                input (event) {
                  // 
                  // 
                  params.item[keyArr[keyArr.length - 1]] = event.target.value
                  vm.data[params.index] = params.item
                }
              }
            }, key)
          }
        })
      }
      // 
      if (!btnsOrigin.length && !operatesOrigin.length) {
        return this.data.colData
      }
      // 
      colOpData.push({
        title: this.langConfig['operate'],
        render: (h, params) => {
          let btns = []
          let items = []
          for (let i = 0; i < btnsOrigin.length; i++) {
            let btn = btnsOrigin[i]
            if (btn.bindKey && btn.bindVal) { // 
              let show
              let valType = typeof (btn.bindVal)
              if (!btn.bindCond || btn.bindCond === '=') { // bindKey=bindVal
                show = false
                if (valType === 'number' || valType === 'string' || valType === 'boolean') {
                  if (params.item[btn.bindKey] === btn.bindVal) {
                    show = true
                  }
                } else if (Array.isArray(btn.bindVal)) {
                  for (var val of btn.bindVal) {
                    if (params.item[btn.bindKey] === val) {
                      show = true
                      break
                    }
                  }
                }
              } else if (btn.bindCond === '!=') { // bindKey!=bindVal
                show = true

                if (valType === 'number' || valType === 'string' || valType === 'boolean') {
                  if (params.item[btn.bindKey] === btn.bindVal) {
                    show = false
                  }
                } else if (Array.isArray(btn.bindVal)) {
                  for (val of btn.bindVal) {
                    if (params.item[btn.bindKey] === val) {
                      show = false
                      break
                    }
                  }
                }
              }

              if (!show) {
                continue
              }
            }

            if (btn.isAdmin && !store.getters.isAdmin) { // ,admin:1,user:0
              continue
            }

            btns.push(h('label', {
              style: {
                cursor: 'pointer',
                color: '#3366FF',
                textAlign: 'left',
                marginRight: '5px',
                minWidth: '40px',
                display: 'inline-block'
              },
              on: {
                click: () => {
                  this.$emit(btn.method, params.item, params.index)
                }
              }
            }, btn.txt))
          }
          for (let i = 0; i < operatesOrigin.length; i++) {
            let operate = operatesOrigin[i]
            if (operate.bindKey && params.item[operate.bindKey] !== operate.bindVal) { // 
              continue
            }
            if (operate.isAdmin) { // ,admin:1,user:0
              continue
            }
            items.push(h('DDropdownItem', {
              nativeOn: {
                click: () => {
                  this.$emit(operate.method, params.item, params.index)
                }
              }
            }, operate.txt))
          }
          if (items.length) {
            btns.push(h('DDropdown',
              {
                props: {
                  trigger: 'click'
                }
              },
              [
                h('a', {
                  props: {
                    size: 'small'
                  }
                }, [
                  '',
                  h('Icon', {
                    props: {
                      name: 'chevron-down',
                      size: '14',
                      slot: 'suffix'
                    }
                  })
                ]),
                h('DDropdownMenu', {
                  slot: 'list'
                }, items)
              ]
            ))
          }
          return h('div', btns)
        }
      })
      return colOpData
    }
  },
  methods: {
    showOperate (operate, row) {
      let show = true
      if (operate.bindKey && (row[operate.bindKey] !== operate.bindVal)) { show = false }
      return show
    },
    tableRowClassName ({row, rowIndex}) {
      if (row.className) {
        return row.className
      }
      return ''
    },
    handleRowClick (row) {
      this.$emit('on-row-click', row)
    },
    handleSelectionChange (val) {
      this.$emit('on-selection-change', val)
    },
    handleSelectionAll (val) {
      this.$emit('on-selection-all', val)
    },
    handleOperate (method, index, row) {
      this.$emit(method, index, row)
    },
    handleSizeChange (val) {
      this.$emit('on-size-change', val)
    },
    handleCurrentChange (val) {
      this.$emit('on-current-change', val)
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
  .table{
    position: relative;
    padding-bottom: 30px;
  }
  .tablePadding{
    padding:20px;
  }
</style>



