<template>
  <d-form ref="form" :model="formData" :rules="rules" label-width="100px" style="height: 350px; overflow-y:auto; width: 100%; padding-right: 20px">
    <d-form-item label="">
      <d-switch v-model="formData.nearBy" ></d-switch>
    </d-form-item>
    <!--<d-form-item label="">-->
      <!--<d-switch v-model="formData.single"></d-switch>-->
    <!--</d-form-item>-->
    <d-form-item label=":">
      <d-switch v-model="formData.archive"></d-switch>
    </d-form-item>
    <!--<d-form-item label="">-->
      <!--<d-input placeholder=" brokerId:weight" v-model="formData.weight"/>-->
    <!--</d-form-item>-->
    <d-form-item label="IP">
      <d-input type="textarea" rows="4" v-model="formData.blackList" placeholder="IPIP"/>
    </d-form-item>
  </d-form>
</template>

<script>
import form from '../../mixins/form.js'

export default {
  name: 'producer-config-form',
  mixins: [ form ],
  props: {
    type: 0, // add or edit form
    data: {
      type: Object,
      default: function () {
        return {
          nearBy: false,
          single: false,
          archive: false,
          weight: '',
          blackList: ''
        }
      }
    }
  },
  data () {
    return {
      formData: this.data,
      rules: {

      }
    }
  },
  methods: {
    getFormData () {
      return this.formData
    }
  }
}
</script>

<style scoped>

</style>



