<template>
  <d-form ref="form" :model="formData" :rules="rules" label-width="100px" style="height: 350px; overflow-y:auto; width: 100%; padding-right: 20px">
     <d-form-item label=":">
       <d-switch v-model="formData.archive"></d-switch>
     </d-form-item>
    <d-form-item label=":">
      <d-switch v-model="formData.retry"></d-switch>
    </d-form-item>
    <d-form-item label=":">
      <d-switch v-model="formData.paused"></d-switch>
    </d-form-item>
    <d-form-item label=":">
      <d-switch v-model="formData.nearBy"></d-switch>
    </d-form-item>
    <d-form-item label="(ms):" prop="delay" >
      <d-input style="width: 60%" placeholder=",1" v-model.number="formData.delay"/>
    </d-form-item>
    <d-form-item label="(ms):" prop="ackTimeout">
      <d-input  style="width: 60%" placeholder="10s" v-model.number="formData.ackTimeout"/>
    </d-form-item>
    <d-form-item label=":" prop="batchSize">
      <d-input style="width: 60%"  placeholder="1-10010" v-model.number="formData.batchSize"/>
    </d-form-item>
    <d-form-item label=":">
      <d-input style="width: 60%"  placeholder=",1-50,1" v-model.number="formData.concurrent"/>
    </d-form-item>
    <d-form-item label="IP:">
      <d-input style="width: 60%"  type="textarea" rows="3" v-model="formData.blackList" placeholder="IPIP"/>
    </d-form-item>
    <d-form-item label=":">
      <d-input style="width: 60%"  type="textarea" rows="3" v-model="formData.filters" placeholder=":key1:value1,key2:value2"/>
    </d-form-item>
  </d-form>
</template>

<script>
import form from '../../mixins/form.js'

export default {
  name: 'consumer-config-form',
  mixins: [ form ],
  props: {
    type: 0, // add or edit form
    data: {
      type: Object,
      default: function () {
        return {
          nearBy: false,
          single: false,
          paused: false,
          archive: false,
          retry: true,
          ackTimeout: undefined,
          batchSize: undefined,
          maxRetrys: 0,
          maxRetryDelay: 300000,
          retryDelay: 0,
          useExponentialBackOff: 0,
          backOffMultiplier: 0,
          expireTime: 3.0,
          delay: undefined,
          concurrent: 1,
          blackList: ''
        }
      }
    }
  },
  data () {
    let numberValidator = (rule, value, callback) => {
      // console.log(rule);
      if ((rule.min !== undefined && value < rule.min) || (rule.max !== undefined && value > rule.max)) {
        callback(new Error(rule.hint))
      } else {
        callback()
      }
    }
    return {
      formData: this.data,
      rules: {
        ackTimeout:
            [
              {validator: numberValidator, type: 'number', trigger: 'change', min: 1000, hint: '1s', required: false}
            ],
        delay:
            [
              {validator: numberValidator, type: 'number', trigger: 'change', min: 0, max: 3600000, hint: '03600s', required: false}
            ],
        batchSize:
            [
              {validator: numberValidator, type: 'number', trigger: 'change', max: 100, min: 1, hint: '1~100', required: false}
            ],
        concurrent:
          [
            {validator: numberValidator, type: 'number', trigger: 'change', max: 50, min: 1, hint: '1~50', required: false}
          ]

      }
    }
  },
  methods: {
    getFormData () {
      // this.submitForm()
      return this.formData
    }

  }
}
</script>

<style scoped>

</style>



